import React, { useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { format } from 'date-fns';

import {
  // getPlanCount,
  getUsersCountfilter,
  // getWorkoutCount,
} from "../../api/Dashboard";

const Dashboard = () => {
  const [usersCount, setUsersCount] = useState({
    totalUsers: 0,
    freeUsers: 0,
    paidUsers: 0,
  });

  const [usersCountf, setUsersCountF] = useState({
    newRegistrations: 0,
    totalSubscribers: 0
  });


  // const [planCount, setPlanCount] = useState({ totalPlans: 0 });
  // const [workoutCount, setWorkoutCount] = useState({
  //   totalCount: 0,
  //   activeCount: 0,
  // });

  // Default date (launch date) and filter states
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [filterOption, setFilterOption] = useState("month"); // Options: day, week, month, 3months, year, custom

  // States for custom date range filter
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  // Dummy fallback data
  const dummyData = {
    totalSubscribers: 8500,
    monthlySubscribers: 6000,
    yearlySubscribers: 2500,
    freeUsers: 7000,
    paidUsers: 1500,
    userDemographics: {
      ageGender: [
        { ageRange: "18-24", male: 500, female: 600, other: 50 },
        { ageRange: "25-34", male: 700, female: 800, other: 100 },
      ],
      geographical: [
        { region: "North America", count: 1200 },
        { region: "Europe", count: 1100 },
        { region: "Asia", count: 1500 },
      ],
    },
    launchDate: "2023-01-01",
  };

  // Fetch data based on filter option and selected date or custom range
  useEffect(() => {
    const fetchData = async () => {
      try {

        
        const sdate = format(startDate, 'yyyy-MM-dd');
        const edate = format(endDate, 'yyyy-MM-dd');

        const params =
          filterOption === "custom"
            ? { range: filterOption, startDate :sdate, endDate:edate }
            : { range: filterOption };

        const users = await getUsersCountfilter(params);
        // const plans = await getPlanCount(params);
        // const workouts = await getWorkoutCount(params);
        setUsersCountF(users);
        // setPlanCount(plans);
        // setWorkoutCount(workouts);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchData();
  }, [filterOption, selectedDate, startDate, endDate]);

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  const handleFilterChange = (event) => {
    setFilterOption(event.target.value);
  };

  const handleStartDateChange = (date) => {
    setStartDate(date);
  };

  const handleEndDateChange = (date) => {
    setEndDate(date);
  };

  return (
    <div className="right-side-wrapper bg-light">
      <div className="p-4" style={{ minHeight: "100vh" }}>
        {/* Header & Filters */}
        <header className="mb-4">
          <div className="d-flex flex-column flex-md-row justify-content-between align-items-center">
            <h1 className="h3 text-primary">Dashboard Overview</h1>
           
          </div>
        </header>

        <main>
          {/* User Metrics Cards */}
          <div className="row g-4">
            <div className="col-lg-4 col-md-6">
              <div className="card shadow border-primary">
                <div className="card-body">
                  <h5 className="card-title">Total Users</h5>
                  <p className="card-text display-6">
                    {usersCount.totalUsers || dummyData.freeUsers + dummyData.paidUsers}
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4 col-md-6">
              <div className="card shadow border-success">
                <div className="card-body">
                  <h5 className="card-title">Free Users</h5>
                  <p className="card-text display-6">
                    {usersCount.freeUsers || dummyData.freeUsers}
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4 col-md-6">
              <div className="card shadow border-warning">
                <div className="card-body">
                  <h5 className="card-title">Paid Users</h5>
                  <p className="card-text display-6">
                    {usersCount.paidUsers || dummyData.paidUsers}
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* Subscribers Cards */}
          <div className="row g-4 mt-4">
            <div className="col-lg-4 col-md-6">
              <div className="card shadow border-info">
                <div className="card-body">
                  <h5 className="card-title">Total Subscribers</h5>
                  <p className="card-text display-6">
                    {dummyData.totalSubscribers}
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4 col-md-6">
              <div className="card shadow border-info">
                <div className="card-body">
                  <h5 className="card-title">Monthly Subscribers</h5>
                  <p className="card-text display-6">
                    {dummyData.monthlySubscribers}
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4 col-md-6">
              <div className="card shadow border-info">
                <div className="card-body">
                  <h5 className="card-title">Yearly Subscribers</h5>
                  <p className="card-text display-6">
                    {dummyData.yearlySubscribers}
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* Demographics Section */}
          <div className="row g-4 mt-4">
           <div className="col-lg-6">
              <div className="card shadow">
                <div className="card-header bg-primary text-white">
                  <h5 className="mb-0">Registration &amp; Subscribers</h5>
                </div>
                <div className="card-body">
                <div className="d-flex align-items-center">
              <div className="me-3">
                <select
                  className="form-select"
                  value={filterOption}
                  onChange={handleFilterChange}
                >
                  <option value="day">Day</option>
                  <option value="week">Week</option>
                  <option value="month">Month</option>
                  <option value="3months">3 Months</option>
                  <option value="year">Year</option>
                  <option value="custom">Custom</option>
                </select>
              </div>
              {filterOption === "custom" && (
                <div className="d-flex gap-2">
                  <DatePicker
                    selected={startDate}
                    onChange={handleStartDateChange}
                    className="form-control me-2"
                    dateFormat="yyyy-MM-dd"
                    minDate={new Date(dummyData.launchDate)}
                  />
                  <DatePicker
                    selected={endDate}
                    onChange={handleEndDateChange}
                    className="form-control"
                    dateFormat="yyyy-MM-dd"
                    minDate={startDate}
                  />
                </div>
              )}
            </div>
                    <div className="mb-3 mt-5">
                      <p className="mb-3 d-flex"> <h5 className="card-title">New Registration : {usersCountf?.newRegistrations}</h5></p>
                      <p className="mb-3 d-flex"><h5 className="card-title">Total Subscribers : {usersCountf?.totalSubscribers}</h5> </p>
                    </div>
                </div>
              </div>
            </div>

            {/* Age & Gender */}
            <div className="col-lg-3">
              <div className="card shadow">
                <div className="card-header bg-primary text-white">
                  <h5 className="mb-0">Age &amp; Gender</h5>
                </div>
                <div className="card-body">
                  {dummyData.userDemographics.ageGender.map((group, index) => (
                    <div key={index} className="mb-3">
                      <h6>{group.ageRange}</h6>
                      <p className="mb-1">Male: {group.male}</p>
                      <p className="mb-1">Female: {group.female}</p>
                      <p className="mb-0">Other: {group.other}</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
            {/* Geographical Location */}
            <div className="col-lg-3">
              <div className="card shadow">
                <div className="card-header bg-secondary text-white">
                  <h5 className="mb-0">Geographical Location</h5>
                </div>
                <div className="card-body">
                  {dummyData.userDemographics.geographical.map((location, index) => (
                    <div key={index} className="mb-3">
                      <h6>{location.region}</h6>
                      <p className="mb-0">Users: {location.count}</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
};

export default Dashboard;
