import CustomAxios from "..";

export const createBlogs = async (formData) => {
    try {
      const response = await CustomAxios.post(
        `/blog/create`,
        formData,
      );
      return response;  
    } catch (error) {
      console.error("Error uploading landing:", error);
      throw error;
    }
};
