import React, { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import { useMediaStore } from "../store";
import { getAllMediaSource } from "../api/getMediaDataForPlans";

const ProtectedRoute = ({ element: Element }) => {
  const isAuthenticated =
    localStorage.getItem("isAuthenticated") === btoa("true");

  const { setImageUrls, setVideoUrls, setLoading } = useMediaStore();

  const [isLoading, setLocalLoading] = useState(true);

  useEffect(() => {
    const fetchMediaData = async () => {
      try {
        setLocalLoading(true);
        const bannerData = await getAllMediaSource("banner");
        const videoData = await getAllMediaSource("video");

        // setImageUrls(bannerData.map((item) => item.cdnUrl));
        // setVideoUrls(videoData.map((item) => item.cdnUrl));

        setImageUrls(bannerData.map((item) => {return {label: item?.fileName, value: item.cdnUrl};}));
        setVideoUrls(videoData.map((item) => {return {label: item?.fileName, value: item.cdnUrl};}));


        setLocalLoading(false);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching media data:", error);
        setLocalLoading(false);
      }
    };

    if (isAuthenticated) {
      fetchMediaData();
    }
  }, [isAuthenticated, setImageUrls, setVideoUrls, setLoading]);

  return isAuthenticated ? <Element /> : <Navigate to="/" />;
};

export default ProtectedRoute;
