import CustomAxios from "..";

export const updatePlanTagById = async (id, planTag) => {
  try {
    const response = await CustomAxios.put(`/plans/id/${id}`, {
      planTag,
    });
    return response.data;
  } catch (error) {
    console.error("Error updating plan:", error.message || error);
    throw new Error("Failed to update the plan. Please try again.");
  }
};

