export const updateRecipieTagById = async (id, recipeTag) => {
  try {
    const response = await CustomAxios.put(`/recipe/id/${id}`, {
      recipeTag,
    });
    return response.data;
  } catch (error) {
    console.error(
      `Error fetching plans with tag "${recipeTag}":`,
      error.response?.data || error.message
    );
    throw new Error("Failed to retrieve the plans by tag. Please try again.");
  }
};
