import React from "react";
import { Pagination } from "react-bootstrap";

const CustomPagination = ({ pagination, handlePageChange }) => {
  return (
   <>
   {pagination && (
          <Pagination className="mt-4 justify-content-center">
            <Pagination.First disabled={pagination.currentPage === 1} onClick={() => handlePageChange(1)} />
            <Pagination.Prev disabled={pagination.currentPage === 1} onClick={() => handlePageChange(pagination.currentPage - 1)} />
            {[...Array(pagination.totalPages)].map((_, index) => (
              <Pagination.Item
                key={index + 1}
                active={index + 1 === pagination.currentPage}
                onClick={() => handlePageChange(index + 1)}
              >
                {index + 1}
              </Pagination.Item>
            ))}
            <Pagination.Next disabled={pagination.currentPage === pagination.totalPages} onClick={() => handlePageChange(pagination.currentPage + 1)} />
            <Pagination.Last disabled={pagination.currentPage === pagination.totalPages} onClick={() => handlePageChange(pagination.totalPages)} />
          </Pagination>
        )}
    </>
  );
};

export default CustomPagination;
