import CustomAxios from "..";

export const getUsersCount = async (params = {  }) => {
  try {
    const response = await CustomAxios.get("/admin/users-count",{ params });
    return response.data;
  } catch (error) {
    console.error("Error fetching users count:", error);
    throw error;
  }
};

